a {
  text-decoration: none;
  color: #E0ECFF;
}
.pool-box {
  width: 100%;
  display: flex;
  justify-content: center;
}
.pool-box .pool-info {
  display: flex;
  margin: 0 auto;
}
.pool-box .left {
  border: 1px solid #203B60;
}
.pool-box .pool-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 96px;
  font-weight: normal;
}
.pool-box .pool-title .select-pool-ves {
  display: none;
}
.pool-box .pool-title .pool-title-text {
  font-size: 32px;
  line-height: 38px;
  color: #FFFFFF;
}
.pool-box .pool-title .pool-title-select-text {
  margin-left: 100px;
}
.pool-box .pool-title .pool-title-select-text .pool-test-des {
  font-size: 14px;
  line-height: 18px;
  text-align: right;
  width: 240px;
}
.pool-box .pool-title .simple-select-ves {
  display: none;
}
.pool-box .pool-card-claim-bg {
  background: linear-gradient(135deg, rgba(97, 122, 255, 0.1) 0%, rgba(223, 229, 255, 0) 50.52%, rgba(85, 91, 237, 0.05) 100%);
}
.pool-box .pool-card-claim-bg .pool-tab {
  height: 108px;
  border-bottom: 1px solid #203B60;
  position: relative;
}
.pool-box .pool-card-claim-bg .pool-tab .pool-tab-info {
  display: flex;
  height: 100%;
}
.pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .simple-tab-mining {
  display: none;
}
.pool-box .pool-card-claim-bg .pool-tab .filter {
  display: flex;
  align-items: center;
}
.pool-box .pool-card-claim-bg .pool-tab .filter .filter-img {
  cursor: pointer;
}
.pool-box .pool-card-claim-bg .pool-tab .tab-mining {
  display: flex;
  align-items: center;
  font-weight: bold;
}
.pool-box .pool-card-claim-bg .pool-tab .tab-mining .pool-active,
.pool-box .pool-card-claim-bg .pool-tab .tab-mining .pool-unactive {
  box-sizing: content-box;
  padding: 24px 24px;
  line-height: 0;
  font-size: 14px;
  cursor: pointer;
}
.pool-box .pool-card-claim-bg .pool-tab .tab-mining .pool-active {
  color: #FFFFFF;
  background: #203B60;
  border-radius: 4px;
}
.pool-box .pool-card-claim-bg .pool-card {
  display: flex;
  flex-flow: wrap;
  flex-grow: 1;
  padding-left: 24px;
}
.pool-box .pool-card-claim-bg .pool-card .card-box {
  width: 380px;
  height: 484px;
  border-bottom: 1px solid #203B60;
  border-right: 1px solid #203B60;
}
.pool-box .right {
  border: 1px solid #203B60;
  border-left: 0;
  display: none;
}
* {
  box-sizing: border-box;
}
@media only screen and (max-width: 374px) {
  .pool-box .pool-info {
    width: 375px;
  }
  .pool-box .left-header-info {
    padding: 0 24px;
  }
  .pool-box .left-header-info .pool-title-select-text {
    display: none;
  }
  .pool-box .left-header-info .simple-select-ves {
    display: block;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info {
    display: flex;
    justify-content: space-between;
    padding: 0 24px;
    margin: 0 auto;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .simple-tab-mining {
    display: flex;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .tab-mining {
    display: none;
  }
  .pool-box .pool-card-claim-bg .pool-card {
    padding: 0;
  }
  .pool-box .pool-card-claim-bg .pool-card .card-box:nth-child(3n+1) {
    width: 380px;
  }
}
@media only screen and (min-width: 375px) and (max-width: 759px) {
  .pool-box .left,
  .pool-box .right {
    border: 0;
  }
  .pool-box .pool-info {
    width: 375px;
  }
  .pool-box .left-header-info {
    padding: 0 24px;
  }
  .pool-box .left-header-info .pool-title-select-text {
    display: none;
  }
  .pool-box .left-header-info .simple-select-ves {
    display: block;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info {
    display: flex;
    justify-content: space-between;
    padding: 0 24px;
    margin: 0 auto;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .simple-tab-mining {
    display: flex;
    align-items: center;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .simple-tab-mining .pool-active,
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .simple-tab-mining .pool-unactive {
    padding: 24px 24px;
    line-height: 0;
    font-size: 14px;
    height: 48px;
    cursor: pointer;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .simple-tab-mining .pool-active {
    color: #FFFFFF;
    background: #203B60;
    border-radius: 4px;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info .tab-mining {
    display: none;
  }
  .pool-box .pool-card-claim-bg .pool-card {
    padding: 0;
    margin-bottom: 8px;
  }
  .pool-box .pool-card-claim-bg .pool-card .card-box:nth-child(3n+1) {
    width: 375px;
  }
  .pool-box .pool-card-claim-bg .pool-card .card-box {
    width: 375px;
    border-right: 0;
  }
}
@media only screen and (min-width: 760px) and (max-width: 1139px) {
  .pool-box .pool-info {
    width: 760px;
  }
  .pool-box .left-header-info {
    padding: 0 24px;
  }
  .pool-box .left-header-info .pool-title-select-text {
    display: none;
  }
  .pool-box .left-header-info .select-pool-ves {
    display: block;
    width: 330px;
    height: 56px;
    background: #203B60;
    border-radius: 8px;
    line-height: 56px;
  }
  .pool-box .pool-card-claim-bg {
    min-height: 1216px;
  }
  .pool-box .pool-card-claim-bg .pool-tab .pool-tab-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 24px;
    margin: 0 auto;
  }
  .pool-box .pool-card-claim-bg .pool-card {
    padding: 0;
    width: 760px;
  }
  .pool-box .pool-card-claim-bg .pool-card .card-box:nth-child(3n+1) {
    width: 380px;
  }
  .pool-box .pool-card-claim-bg .pool-card .card-box:nth-child(3n) {
    border-right: 1px solid #203B60;
  }
  .pool-box .pool-card-claim-bg .pool-card .card-box:nth-child(2n) {
    border-right: 0;
  }
}
@media only screen and (min-width: 1140px) and (max-width: 1567px) {
  .pool-box {
    display: flex;
    flex-direction: column;
  }
  .pool-box .left {
    border-top: 1px solid #203B60;
    border-bottom: 1px solid #203B60;
  }
  .pool-box .left .left-header-info {
    width: 1140px;
    margin: 0 auto;
    padding: 0 24px;
  }
  .pool-box .left .left-header-info .select-pool-ves {
    display: block;
    width: 332px;
    height: 56px;
    background: #203B60;
    border-radius: 8px;
    line-height: 56px;
  }
  .pool-box .left .pool-card-claim-bg .pool-tab .pool-tab-info {
    display: flex;
    width: 1140px;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 24px;
  }
  .pool-box .left .pool-card-claim-bg .pool-card {
    padding-left: 0;
    width: 1140px;
  }
  .pool-box .left .pool-card-claim-bg .pool-card .card-box:nth-child(3n+1) {
    width: 380px;
    margin-left: 0px;
  }
  .pool-box .left .pool-card-claim-bg .pool-card .card-box:first-child {
    border-right: 1px solid #203B60;
  }
  .pool-box .left .pool-card-claim-bg .pool-card .card-box:nth-child(3n) {
    border-right: 0;
  }
}
@media only screen and (min-width: 1568px) {
  .pool-box {
    flex-direction: initial;
  }
  .pool-box .left {
    margin: 0;
    border-top: 1px solid #203B60;
  }
  .pool-box .left .left-header-info {
    width: 1164px;
    padding-left: 48px;
  }
  .pool-box .left .left-header-info .pool-title {
    padding-right: 40px;
  }
  .pool-box .left .pool-card-claim-bg {
    width: 1164px;
  }
  .pool-box .left .pool-card-claim-bg .pool-tab {
    padding-left: 48px;
  }
  .pool-box .left .pool-card-claim-bg .pool-tab .filter {
    display: none;
  }
  .pool-box .left .pool-card .card-box {
    border-right: 1px solid #203B60;
  }
  .pool-box .left .pool-card .card-box:nth-child(3n+1) {
    width: 403px;
    padding-left: 24px;
    margin-left: -24px;
  }
  .pool-box .left .pool-card .card-box:nth-child(3n+1) .card-header {
    width: 403px;
    margin-left: -24px;
    padding-left: 48px;
  }
  .pool-box .left .pool-card .card-box:nth-child(3n) {
    border-right: 0px;
  }
  .pool-box .right {
    display: block;
  }
  .pool-box .right .right-info {
    width: 100%;
    height: 1312px;
  }
  .pool-box .right .right-info .select-pool-ves-staked {
    border-bottom: 1px solid #203B60;
    height: 176px;
    width: 100%;
    padding-right: 24px;
    padding-left: 24px;
    padding-top: 37px;
  }
  .pool-box .right .right-info .select-pool-ves-staked .l-toggle .react-toggle-thumb {
    background: #203B60;
  }
  .pool-box .right .right-info .select-pool-ves-staked .l-toggle .react-toggle--checked .react-toggle-thumb {
    background: #5577FD;
  }
  .pool-box .right .right-info .select-pool-ves-staked .select-pool-ves {
    width: 332px;
    height: 56px;
    background: #203B60;
    border-radius: 8px;
    line-height: 56px;
  }
  .pool-box .right .right-info .select-pool-ves-staked .staked-only {
    width: 332px;
    display: flex;
    justify-content: space-between;
    margin-top: 39px;
  }
  .pool-box .right .right-info .select-pool-ves-staked .staked-only .staked-only-text {
    color: #FFFFFF;
  }
  .pool-box .right .right-info .select-pool-type-network {
    padding-left: 24px;
    border-bottom: 1px solid #203B60;
  }
  .pool-box .right .right-info .select-pool-type-network .pool-type {
    width: 380px;
    height: 184px;
  }
  .pool-box .right .right-info .select-pool-type-network .type-cols {
    width: 380px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 48px;
  }
  .pool-box .right .right-info .select-pool-type-network .type-cols .type-title {
    font-size: 14px;
    color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .pool-box .right .right-info .select-pool-type-network .type-cols .type-title img {
    margin-right: 10px;
    width: 22px;
    height: 22px;
  }
  .pool-box .right .right-info .select-pool-type-network .network {
    margin-top: 8px;
    margin-bottom: 16px;
  }
  .pool-box .right .right-info .select-footer {
    color: #5577FD;
    font-size: 18px;
  }
  .pool-box .right .right-info .select-footer .clear-all-filter {
    text-align: center;
    cursor: pointer;
    padding-top: 21px;
    height: 309px;
  }
  .pool-box .right .right-info .select-footer .deri-logo {
    height: 484px;
    position: relative;
    left: -1px;
  }
  .pool-box .right .right-info .select-footer .deri-logo img {
    margin: 0 auto;
  }
}
