a {
  text-decoration: none;
  color: #E0ECFF;
}
.card-box {
  position: relative;
}
.new-symbol {
  width: 362px;
  height: 31px;
  position: absolute;
  top: 90px;
  left: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 5px;
  background: #222021;
  padding: 0 14px;
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  color: #FFB110;
}
.new-symbol .close-symbol {
  cursor: pointer;
}
.card-list {
  cursor: pointer;
}
.card-list a {
  text-decoration: none;
  background: url('../../assets/img/pool-card-background.svg');
}
.card-list .card-header.v3 {
  background-image: url(../../assets/img/new-label.svg);
  background-repeat: no-repeat;
  background-position: right top;
}
.card-list .card-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  border-bottom: 1px solid #203B60;
  padding: 0 24px;
}
.card-list .card-header .pool-network-type {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-list .card-header .pool-network-type .pool-type-network-text {
  margin-left: 12px;
  font-weight: 600;
}
.card-list .card-header .pool-network-type .pool-type-network-text .pool-type-text {
  color: #FFFFFF;
}
.card-list .card-header .pool-network-type .pool-type-network-text .pool-network-text {
  font-size: 14px;
}
.card-list .card-header .pool-network-type .network-logo img {
  width: 28px;
  height: 28px;
}
.card-list .card-header .base-token-logo {
  height: 48px;
}
.card-list .card-header .base-token-logo .v3-tokens {
  display: flex;
}
.card-list .card-header .base-token-logo .v3-tokens .tokens {
  overflow: hidden;
  width: 120px;
  white-space: nowrap;
}
.card-list .card-header .base-token-logo .base-token-box-bg {
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  background-color: #0b182d;
  position: relative;
}
.card-list .card-header .base-token-logo .base-token-box-bg .base-token-bg {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  color: #E0ECFF;
  align-items: center;
  position: absolute;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.card-list .card-header .base-token-logo .base-token-box-bg .base-token-bg img {
  width: 20px;
  height: 20px;
}
.card-list .card-header .base-token-logo .base-token-box-bg:nth-child(n) {
  margin-left: -12px;
}
.card-list .card-header .base-token-logo .base-token-box-bg:first-child {
  margin-left: 0;
}
.card-list .card-header .base-token-logo .lp-base-token-bg {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
}
.card-list .card-header .base-token-logo .lp-base-token-bg .lp-con {
  margin: 0 5px;
}
.card-list .card-header .base-token-logo .lp-base-token-bg .base-token-bg {
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  display: flex;
  align-items: center;
}
