.card-list .card-content {
  height: 404px;
  padding: 48px 24px 40px 24px;
}
.card-list .card-content .max-apy {
  height: 67px;
  margin-top: 8px;
}
.card-list .card-content .max-apy-title,
.card-list .card-content .total-liquidity-title {
  font-size: 14px;
}
.card-list .card-content .max-apy-num,
.card-list .card-content .total-liquidity-num {
  font-size: 36px;
  color: #E0ECFF;
  margin-top: 5px;
  height: 45px;
}
.card-list .card-content .total-pool-liquidity {
  margin-top: 24px;
}
.card-list .card-content .your-deri-epoch {
  font-size: 12px;
  height: 20px;
  margin-top: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.card-list .card-content .your-deri-epoch span {
  margin-left: 8px;
}
.card-list .card-content .your-deri-epoch .staked-box {
  width: 80px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px;
  border: 1px solid rgba(224, 236, 255, 0.5);
  border-radius: 100px;
}
.card-list .card-content .your-deri-epoch .xvs-collectable {
  display: flex;
  align-items: center;
  margin-left: 0;
}
.card-list .card-content .your-deri-epoch .xvs-collectable .staked-box {
  width: 100%;
}
.card-list .card-content .stake-button {
  margin-top: 24px;
  border: 1px solid transparent;
}
.card-list .card-content .stake-button .button {
  width: 332px;
  height: 56px;
  border: 0;
  background: rgba(255, 255, 255, 0.1);
  color: #FFFFFF;
  border-radius: 4px;
  font-size: 18px;
  font-weight: 600;
}
.open-list {
  cursor: not-allowed;
}
.card-box:hover {
  background: linear-gradient(to right bottom, #213156, rgba(21, 37, 67, 0.5));
  background-repeat: no-repeat;
  background-size: 102%;
}
.card-box:hover .stake-button {
  width: 332px;
  background-color: rgba(85, 119, 253, 0.1);
  border: 1px solid #CD7A37;
  border-radius: 4px;
}
.card-box:first-child .card-header .base-token-box-bg {
  background-color: #111e34;
}
